/* Global styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #FFEB3B;  /* Soft Yellow for background */
  color: #333;  /* Dark gray text for good contrast */
}

h2, h3 {
  color: #333;  /* Dark gray for headings */
}

nav {
  background-color: #333;  /* Dark navbar for contrast */
  padding: 10px;
  display: flex;
  justify-content: space-around;
}

nav a {
  color: #fff;
  text-decoration: none;
  padding: 8px 15px;
  border-radius: 5px;
}

nav a:hover {
  background-color: #555;
}

/* Coupon List Styles */
.coupon-list {
  display: flex;
  flex-wrap: wrap; /* This ensures the coupons wrap to the next line when necessary */
  gap: 20px; /* Adds space between the coupon cards */
  justify-content: center; /* Centers the coupon cards horizontally */
  padding: 20px; /* Adds some padding around the container */
}

.coupon-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

/* Coupon Card Styles */
.coupon-card {
  background-color: #FFFFFF;  /* White background for the card */
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  width: 250px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.coupon-card:hover {
  transform: translateY(-10px);  /* Slight lift on hover */
}

/* Coupon Code Styles */
.coupon-code {
  font-size: 24px;
  font-weight: bold;
  color: #FF5722;  /* Bright Orange-Red color for the code */
}

.coupon-discount {
  font-size: 18px;
  margin: 10px 0;
  color: #4CAF50;  /* Green for the discount percentage */
}

.coupon-expiry {
  font-size: 14px;
  color: #888;  /* Lighter gray for expiry */
}

.coupon-link {
  display: inline-block;
  margin-top: 10px;
  text-decoration: none;
  color: #2196F3;  /* Blue color for the link */
}

.coupon-link:hover {
  text-decoration: underline;
}

/* Form Styles */
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin: 20px auto;
  max-width: 400px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.navbar {
  background-color: #ffcc00; /* Set the background color to yellow */
  padding: 10px 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Slight shadow for depth */
}

/* Navbar container */
.navbar-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

/* Logo style */
.logo a {
  font-size: 1.8em;
  font-weight: bold;
  color: #333; /* Dark gray text */
  text-decoration: none;
}

.logo a:hover {
  color: #000; /* Change color on hover */
}

/* Navbar links container */
.nav-links {
  list-style-type: none;
  display: flex;
  gap: 20px;
}

/* Navbar link styling */
.nav-link {
  color: #333; /* Dark gray color */
  text-decoration: none;
  font-size: 1.1em;
  font-weight: 600;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: #ff5722; /* Orange color on hover */
}

/* Responsive navbar (for smaller screens) */
@media screen and (max-width: 768px) {
  .navbar-container {
    flex-direction: column;
    align-items: center;
  }

  .nav-links {
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
  }

  .nav-link {
    font-size: 1.2em;
  }
}
.coupon-card {
  background-color: #FFFFFF;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  width: 250px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
