/* Styling for the About page */
.about-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  text-align: center;
  background-color: #f9f9f9; /* Light background for contrast */
  padding: 20px;
  font-family: 'Arial', sans-serif;
}

/* Add some spacing and make the text more readable */
.about-page h2 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 20px;
}

.about-page p {
  font-size: 1.2rem;
  color: #555;
  max-width: 800px; /* Limit the text width for better readability */
  line-height: 1.6;
  margin-bottom: 20px;
  text-align: justify;
}

/* Add a bit of decoration for the section */
.about-page p:last-child {
  font-weight: bold;
  color: #FF5722; /* A bright color for emphasis */
}

/* You can also add a border or box-shadow for visual appeal */
.about-page-container {
  padding: 30px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 900px; /* Make sure it doesn't stretch too wide */
}
